: flex;\n}","$color-main-background: #f8f8f8;\n$color-main-turquoise: #41f6d7;\n$color-stroke-turquoise: #b8ffec;\n$color-secondary-turquoise: #d9fff5;\n$color-white: #fff;\n$color-stroke-grey: #eaeaea;\n$color-dark-grey: #b5b6bc;\n$color-main-pink: #ff3e79;\n$color-secondary-black: rgba(224, 224, 224, 0);\n$text-color-main-black: #1b1c21;\n$text-color-main-grey: #7e7e82;","/* БЛОК САЙДБАР МЕНЮ */\n// ОБЩЕЕ\n.sidebar-header {\n  z-index: 2;\n  width: 320px;\n  position: fixed;\n  background-color: $color-white;\n  box-shadow:\n    16px 0px 52px 0px rgba(14, 24, 80, 0.2),\n    -2px 0px 4px 0px rgba(69, 79, 126, 0.02);\n  overflow-y: auto;\n  left: -150%;\n  transition: left 500ms ease-in-out;\n  display: flex;\n  flex-direction: column;\n  height: 100%;\n\n  &--open {\n    left: 0;\n    height: 100%;\n  }\n\n  @media (min-width: 360px) {\n    min-width: 360px;\n  }\n\n  @media (min-width: 768px) {\n    min-width: 320px;\n    width: 320px;\n  }\n\n  @media (min-width: 1440px) {\n    display: block;\n    position: static;\n    box-shadow: none;\n    z-index: 1;\n    height: auto;\n  }\n\n  /* ШАПКА */\n  &__head {\n    padding: 18.73px 16px 0 16px;\n\n    @media (min-width: 360px) {\n      padding: 18.73px 24px 0 24px;\n    }\n\n    @media (min-width: 768px) {\n      padding: 18.73px 24px 0 24px;\n    }\n\n    @media (min-width: 1440px) {\n      padding-top: 34px;\n    }\n  }\n\n  &__menu-container {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n  }\n\n  &__menu-container-part1 {\n    display: flex;\n    flex-direction: row;\n    gap: 16.73px;\n  }\n\n  &__menu-container-part2 {\n    display: flex;\n    flex-direction: row;\n  }\n\n  &__menu-button {\n    margin-top: 5.73px;\n\n    &:focus,\n    &:hover {\n      opacity: 0.6;\n      transition: 0.2s ease-in-out;\n      cursor: pointer;\n    }\n  }\n\n  /* НАВИГАЦИЯ */\n  &__navigation {\n    margin: 26px 0 241px 0;\n    padding: 0 16px 0 16px;\n  }\n\n  &__list {\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n    margin-left: -16px;\n    gap: 10px;\n\n    @media (min-width: 360px) {\n      margin-left: -24px;\n    }\n  }\n\n  &__item {\n    padding-top: 4px;\n    padding-bottom: 4px;\n\n    @media (min-width: 768px) {\n      padding-top: 7px;\n      padding-bottom: 7px;\n    }\n\n    &:focus,\n    &:hover {\n      content: \"\";\n      width: 4px;\n      background-color: $color-main-turquoise;\n      border-radius: 0px 10px 10px 0px;\n      white-space: nowrap;\n      transition: 0.2s ease-in-out;\n    }\n  }\n\n  &__link {\n    color: $text-color-main-black;\n    font-family: TT Lakes;\n    font-size: 16px;\n    font-style: normal;\n    font-weight: 500;\n    line-height: 150%;\n    letter-spacing: -0.2px;\n    cursor: pointer;\n    padding: 0 0 0 16px;\n\n    @media (min-width: 360px) {\n      padding: 0 0 0 24px;\n    }\n\n    &:focus,\n    &:hover {\n      color: $text-color-main-grey;\n      transition: 0.2s ease-in-out;\n    }\n  }\n\n  /* ПОДВАЛ */\n  &__footer {\n    padding: 0 16px 32px 16px;\n    margin-top: auto;\n  }\n\n  &__footer-list {\n    display: flex;\n    justify-content: flex-start;\n    gap: 16px;\n  }\n\n  &__footer-contact {\n    display: flex;\n    flex-direction: column;\n    justify-content: flex-start;\n    gap: 12px;\n    margin: 16px 0 8px 0;\n\n    @media (min-width: 768px) {\n      margin-bottom: 40px;\n    }\n  }\n\n  &__number {\n    font-family: TT Lakes;\n    font-size: 24px;\n    font-style: normal;\n    font-weight: 700;\n    cursor: pointer;\n  }\n\n  &__mail {\n    cursor: pointer;\n  }\n\n  &__language-list {\n    display: flex;\n    justify-content: flex-start;\n    gap: 7px;\n  }\n\n  &__language-button {\n    &--active {\n      color: text-color-main-grey;\n    }\n  }\n\n  &__language-button {\n    &:focus,\n    &:hover {\n      color: $text-color-main-grey;\n      cursor: pointer;\n      transition: 0.2s ease-in-out;\n    }\n  }\n\n  &__footer-button {\n    &:focus,\n    &:hover {\n      opacity: 0.6;\n      transition: 0.2s ease-in-out;\n      cursor: pointer;\n    }\n  }\n}\n\n/* ПОЯВЛЕНИЕ САЙДБАР МЕНЮ ПОСЛЕ НАЖАТИЯ НА КНОПКУ МЕНЮ */\n.button-menu--open {\n  height: 100%;\n  left: 0;\n}\n\n/* ПОЯВЛЕНИЕ САЙДБАР МЕНЮ ПОСЛЕ 1440 ПХ+ */\n.button-menu--close {\n  @media (min-width: 1440px) {\n    display: none;\n  }\n}\n",".section__title-hidden {\n  visibility: hidden;\n}\n\n.section {\n  &__all-content {\n    min-width: 320px;\n  }\n}\n\n/* ШАПКА САЙТА */\n\n.header {\n  background: $color-white;\n  border-bottom: 2px solid $color-secondary-turquoise;\n\n  @media (min-width: 1440px) {\n    display: none;\n  }\n\n  &__divider {\n    display: none;\n    @media (min-width: 768px) {\n      display: block;\n      content: \"\";\n      position: relative;\n      top: 0px;\n      height: 32px;\n      border: 2px solid $color-stroke-grey;\n    }\n  }\n\n  &__container {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    width: 90%;\n    margin: 0 auto;\n    min-height: 88px;\n    @media (min-width: 768px) {\n      width: 95%;\n    }\n  }\n\n  &__logo-side {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    gap: 16px;\n    &::after {\n      position: relative;\n      content: \"\";\n      height: 32px;\n      border: 2px solid $color-stroke-grey;\n      top: 0px;\n    }\n    @media (min-width: 768px) {\n      gap: 18px;\n      &::after {\n        display: none;\n      }\n    }\n  }\n\n  &__contacts-side {\n    display: flex;\n    justify-content: center;\n    gap: 16px;\n  }\n\n  &__button-item-connection {\n    display: none;\n    @media (min-width: 768px) {\n      display: flex;\n      gap: 16px;\n      &::after {\n        position: relative;\n        content: \"\";\n        height: 32px;\n        border: 2px solid $color-stroke-grey;\n        top: 3px;\n      }\n    }\n  }\n\n  &__button-item {\n    &:focus,\n    &:hover {\n      opacity: 0.5;\n      transition: 0.2s;\n    }\n  }\n}\n\n.header__logo-side > .header__button-item:nth-child(2) {\n  @media (min-width: 768px) {\n    margin-left: 16px;\n  }\n}\n","/* 1 СЕКЦИЯ MAIN-SECTION */\n\n.main-container {\n  margin-bottom: 24px;\n  // ЗАГОЛОВОК\n  &__title {\n    margin-bottom: 24px;\n\n    @media (min-width: 768px) {\n      margin-bottom: 32px;\n    }\n\n    @media (min-width: 1120px) {\n      display: flex;\n      justify-content: space-between;\n      margin: 0 32px 32px 0;\n      align-items: center;\n    }\n\n    @media (min-width: 1440px) {\n      margin-bottom: 8.37px;\n    }\n  }\n\n  /* ОСНОВНАЯ НАВИГАЦИЯ */\n  &__navigation {\n    margin: 0 16px 32px 16px;\n\n    @media (min-width: 768px) {\n      margin-left: 24px;\n    }\n\n    @media (min-width: 1120px) {\n      margin-left: 32px;\n    }\n  }\n\n  &__navigation-list {\n    display: flex;\n    overflow-x: scroll;\n\n    /*  скрол у навигации */\n    &::-webkit-scrollbar {\n      display: none;\n    }\n\n    @media (min-width: 1120px) {\n      flex-wrap: wrap;\n    }\n  }\n  /* ТЕКСТ СТРАНИЦЫ И КАРТИНКА */\n  &__content {\n    display: flex;\n    flex-direction: column;\n\n    @media (min-width: 768px) {\n      display: flex;\n      flex-direction: row;\n      margin: 0 auto;\n      width: 95%;\n    }\n  }\n\n  &__content-text {\n    display: flex;\n    flex-direction: column;\n    gap: 16px;\n    margin: 0 auto;\n    width: 90%;\n    padding: 0 16px;\n  }\n\n  &__text {\n    padding: 0 16px;\n    display: flex;\n    flex-direction: column;\n    height: 134.5px;\n    overflow: hidden;\n    transition: height 0.5s ease;\n    gap: 10px;\n    max-width: 90%;\n    color: $text-color-main-black;\n    font-family: TT Lakes;\n    font-size: 14px;\n    font-style: normal;\n    font-weight: 400;\n    line-height: 23.9px;\n    letter-spacing: 0.2px;\n\n    @media screen and (min-width: 768px) {\n      padding: 0;\n    }\n  }\n\n  /* Кнопка в секции */\n  &__read-more-button {\n    margin: 0 0 16px 16px;\n    display: flex;\n    gap: 8px;\n    width: 140px;\n\n    @media screen and (min-width: 768px) {\n      margin: 0 0 16px 0;\n    }\n\n    &:hover,\n    &:focus {\n      opacity: 0.6;\n    }\n  }\n\n  // АНИМАЦИЯ КНОПКИ ПРИ НАЖАТИИ\n  &__button-svg-arrows {\n    &--hide {\n      transform: rotate(180deg);\n      transition-duration: 0.2s;\n    }\n  }\n\n  &__button-text {\n    color: $text-color-main-black;\n    font-family: TT Lakes;\n    font-size: 16px;\n    font-style: normal;\n    font-weight: 500;\n    line-height: 24px; /* 150% */\n    letter-spacing: -0.2px;\n  }\n\n  /* Картинка в секции */\n  &__illustraton {\n    display: flex;\n    justify-content: center;\n  }\n\n  &__illustraton-1 {\n    width: 100%;\n    height: auto;\n\n    @media (min-width: 768px) {\n      border-radius: 10px;\n      width: 360px;\n      height: 208px;\n    }\n\n    @media (min-width: 1440px) {\n      border-radius: 10px;\n      width: 532px;\n      height: 308px;\n    }\n  }\n}\n\n// НАВИГАЦИЯ\n.navigation-list {\n  &__item {\n    border: 2px solid transparent;\n    color: $text-color-main-grey;\n    white-space: nowrap;\n    padding: 8px 16.67px 8px 16.67px;\n\n    &:focus,\n    &:hover {\n      background-color: $color-white;\n      border-radius: 6px;\n      border-color: $color-stroke-turquoise;\n    }\n  }\n\n  &__button {\n    color: $text-color-main-grey;\n    font-size: 16px;\n    font-style: normal;\n    font-weight: 500;\n  }\n}\n\n// ЗАГОЛОВОК\n.title-number1 {\n  &__head {\n    color: $text-color-main-black;\n    font-family: TT Lakes;\n    font-size: 28px;\n    font-style: normal;\n    font-weight: 700;\n    line-height: 40px; /* 142.857% */\n    letter-spacing: -0.6px;\n    padding: 24px 16px 0 16px;\n\n    &::before {\n      content: \"\";\n      position: absolute;\n      height: 40px;\n      width: 4px;\n      left: 0;\n      background-color: $color-main-turquoise;\n      border-radius: 0px 10px 10px 0px;\n    }\n\n    @media (min-width: 768px) {\n      padding-left: 24px;\n 